.fra-home-page {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.fra-featured {
  flex: 1;
  display: flex;

  .half {
    flex: 1;
    display: flex;
    padding: 10px;

    &.left-half {
      padding-left: 15vw;
      align-items: center;
    }

    &.right-half {
      padding-right: 15vw;
      align-items: flex-end;
      justify-content: flex-end;
    }
  }

  .fra-featured-screenshot {
    height: 500px;
    border-radius: 10px;
    border: 20px black solid;
    position: relative;
    bottom: -50px;
  }
}

.fra-footer {
  flex: 0 200px;
  display: flex;
  align-items: center;
  background-color: white;
  padding: 0px 100px;

  .fra-store-badge {
    width: 230px;
    height: 100px;
    border: none;
    outline: none;
    background-color: transparent;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &.google {
      width: 250px;
      height: 110px;
      background-image: url('../../images/google-play-badge.png');
    }

    &.apple {
      background-size: contain;
      background-image: url('../../images/apple-store-badge.svg');
    }

    &:hover {
      cursor: pointer;
    }
  }
}

@media only screen and (max-width: 750px) {
  .fra-featured-game {
    font-size: 6em !important;
  }

  .fra-featured {
    flex-direction: column;

    .half {
      &.left-half {
        padding-left: 20px;
      }
      &.right-half {
        padding-right: 0;
        justify-content: center;
      }
    }
  }

  .fra-footer {
    padding: 0;
    justify-content: center;
  }
}
