.fra-featured-game-container {
  display: inline-flex;
  flex-direction: column;
  font-family: 'Loosey Sans';

  .fra-featured-game {
    font-size: 9em;
    font-weight: 900;
    color: white;
    -webkit-text-stroke-width: 5px;
    -webkit-text-stroke-color: white;
    text-shadow: 0.0625em -0.03125em #ea712b, 0.09375em 0.04255em #ea712b, 0.11458em 0.07291em #000, -0.04255em 0.04255em #ea712b;
  }
}
